import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { HashRouter, Link } from 'react-router-dom';

class Main extends Component {
    handleClick = link => {
        if (window.confirm("Open in same window click Yes ! or click cancel to open new tab")) {
            document.location.href=link;
        } else {
            window.open(link);
        }
    }
    render() {
        return (
            // <!-- HEADER -->
            <header className="header">
                <div className="container">
                    <div className="row">
                    <div className="col-12 col-lg-6 typography">
                        <div className="jumbotron">
                            <h1 className="display-4">
                                Halo ! <br />
                                Saya Fauzan
                            </h1>
                            <p className="lead">
                                Saya adalah seorang Designer dengan latar belakang pendidikan S1
                                Teknik informatika dengan keahlian Frontend Designer, UI UX
                                Designer dan Graphic Designer. Saya sangat senang memecahkan
                                sebuah masalah guna mendapatkan solusi pada sebuah desain yang
                                dapat memberi manfaat bagi orang lain.
                            </p>
                            <HashRouter>
                            <Link
                                className="btn btn-blue me-2"
                                to="/portofolio"
                                role="button"
                                >My Portofolio
                            </Link>
                            </HashRouter>
                            
                            <a className="btn btn-outline-blue" href="/pdf" download="cv.pdf" target="_blank"> Download CV
                            </a>

                            {/* <div
                                className="btn btn-outline-blue"
                                onClick={(e) => this.handleClick("https://www.instagram.com/_fauzanahmad_")}
                                role="button"
                                >Follow Me 
                            </div> */}
                            </div>
                            <div className="social-media">
                                <span onClick={(e) => this.handleClick("https://www.instagram.com/_fauzanahmad_")}
                                    className="sm-item link"
                                    >
                                        <i><FontAwesomeIcon icon={['fab', 'instagram']} /></i>
                                </span>
                                <span onClick={(e) => this.handleClick("https://www.codepen.io/_fauzanahmad_")}
                                    className="sm-item link"
                                    ><i><FontAwesomeIcon icon={['fab', 'codepen']} /></i>
                                </span>
                                <span onClick={(e) => this.handleClick("https://github.com/fauzanahmad11")}
                                    className="sm-item link">
                                        <i><FontAwesomeIcon icon={['fab', 'github']} /></i>
                                </span>
                            </div>
                    </div>
                    <div className="col-12 col-lg-6 text-center profile">
                        <div className="img">
                        <img src="/img/profile-1.svg" className="img-fluid" alt="" />
                        </div>
                        <div className="copyright">
                        <small><span className="text-logo">FAFA</span> &copy; 2020</small>
                        </div>
                    </div>
                    </div>
                </div>
            </header>
        );
    }
}

export default Main;